// Per-view TypeScript file for Home/Index
// This demonstrates per-view TypeScript compilation

interface Feature {
    name: string;
    description: string;
    status: 'working' | 'testing' | 'planned';
}

const features: Feature[] = [
    { name: 'TypeScript Support', description: 'Auto-detect and compile .ts/.tsx files', status: 'working' },
    { name: 'SCSS Support', description: 'Auto-detect and compile .scss/.sass files', status: 'working' },
    { name: 'Mixed Projects', description: 'Use JS + TS and CSS + SCSS together', status: 'working' },
    { name: 'Zero Config', description: 'Just use the file extensions', status: 'working' }
];

function renderFeatures(): void {
    const container = document.getElementById('features-container');
    if (!container) {
        console.warn('Features container not found');
        return;
    }

    const list = document.createElement('ul');
    list.className = 'feature-list';

    features.forEach(feature => {
        const li = document.createElement('li');
        li.innerHTML = `
            <span class="icon">${getStatusIcon(feature.status)}</span>
            <strong>${feature.name}</strong>: ${feature.description}
        `;
        list.appendChild(li);
    });

    container.appendChild(list);
}

function getStatusIcon(status: Feature['status']): string {
    switch (status) {
        case 'working': return '✓';
        case 'testing': return '⚡';
        case 'planned': return '○';
    }
}

// Initialize on DOMContentLoaded
document.addEventListener('DOMContentLoaded', () => {
    console.log('[Home/Index] Per-view TypeScript loaded');
    renderFeatures();
});

export { features, renderFeatures };
